import Home0 from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';
import Core from './core';
import Guide from './guide';
import Api from './api';
import Hooks from './hooks';
// import { configure } from '@testing-library/react';
// import { cssHooks } from 'jquery';
const routes = [
    {
        path:'/',
        exact:true,
        component:Home0
    },
    {
        path:'/doc',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            }
        ]
    },
    {
        path:'/tuto',
        component:Tuto,
        pri:true
    },
    {
        path:'/blog',
        component:Blog
    },
    {
        path:'/comm',
        component:Comm
    },
    {
        path:'/login',
        component:Login
    },
]
export default routes;